"use client"

import { useEffect, useState } from "react"
import { dataStore } from "@/lib/data-store"
import { GiftCard } from "@/lib/types"

export default function AdminGiftCards() {
  const [giftCards, setGiftCards] = useState<GiftCard[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [filter, setFilter] = useState<'all' | 'active' | 'redeemed' | 'expired'>('all')

  useEffect(() => {
    try {
      const allGiftCards = dataStore.getAllGiftCards()
      setGiftCards(allGiftCards)
    } catch (err) {
      setError("Failed to load gift cards")
      console.error("Gift cards error:", err)
    } finally {
      setLoading(false)
    }
  }, [])

  const filteredCards = giftCards.filter(card => {
    if (filter === 'all') return true
    return card.status === filter
  })

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(amount)
  }

  const formatDate = (date: Date) => {
    return new Date(date).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  const getStatusBadge = (status: string) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full"
    switch (status) {
      case 'active':
        return `${baseClasses} bg-green-100 text-green-800`
      case 'redeemed':
        return `${baseClasses} bg-blue-100 text-blue-800`
      case 'expired':
        return `${baseClasses} bg-red-100 text-red-800`
      default:
        return `${baseClasses} bg-gray-100 text-gray-800`
    }
  }

  const getUsagePercentage = (remaining: number, total: number) => {
    const used = total - remaining
    return Math.round((used / total) * 100)
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <p className="text-red-800">{error}</p>
      </div>
    )
  }

  return (
    <div>
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">Gift Cards Management</h1>
        <p className="text-gray-600 mt-2">Track and manage digital gift cards</p>
      </div>

      {/* Filter Tabs */}
      <div className="mb-6">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8">
            {[
              { key: 'all', label: 'All Cards', count: giftCards.length },
              { key: 'active', label: 'Active', count: giftCards.filter(c => c.status === 'active').length },
              { key: 'redeemed', label: 'Fully Redeemed', count: giftCards.filter(c => c.status === 'redeemed').length },
              { key: 'expired', label: 'Expired', count: giftCards.filter(c => c.status === 'expired').length },
            ].map((tab) => (
              <button
                key={tab.key}
                onClick={() => setFilter(tab.key as any)}
                className={`py-2 px-1 border-b-2 font-medium text-sm ${
                  filter === tab.key
                    ? 'border-gray-900 text-gray-900'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                {tab.label} ({tab.count})
              </button>
            ))}
          </nav>
        </div>
      </div>

      {/* Gift Cards Table */}
      <div className="bg-white shadow rounded-lg overflow-hidden">
        {filteredCards.length === 0 ? (
          <div className="p-8 text-center">
            <p className="text-gray-500">No gift cards found for the selected filter.</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Code
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Purchaser
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Recipient
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Amount
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Balance
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Created
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredCards.map((card) => (
                  <tr key={card.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-gray-900 font-mono">
                        {card.code}
                      </div>
                      <div className="text-xs text-gray-500">ID: {card.id.slice(-8)}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900">{card.purchaserName}</div>
                      <div className="text-xs text-gray-500">{card.purchaserEmail}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      {card.recipientName ? (
                        <div>
                          <div className="text-sm text-gray-900">{card.recipientName}</div>
                          <div className="text-xs text-gray-500">{card.recipientEmail}</div>
                        </div>
                      ) : (
                        <div className="text-sm text-gray-500 italic">Self-purchase</div>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-gray-900">
                        {formatCurrency(card.amount)}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-gray-900">
                        {formatCurrency(card.remainingBalance)}
                      </div>
                      {card.remainingBalance < card.amount && (
                        <div className="w-full bg-gray-200 rounded-full h-2 mt-1">
                          <div
                            className="bg-blue-500 h-2 rounded-full"
                            style={{
                              width: `${(card.remainingBalance / card.amount) * 100}%`
                            }}
                          ></div>
                        </div>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={getStatusBadge(card.status)}>
                        {card.status}
                      </span>
                      {card.redeemedAt && (
                        <div className="text-xs text-gray-500 mt-1">
                          Redeemed: {formatDate(card.redeemedAt)}
                        </div>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {formatDate(card.createdAt)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                      <div className="flex space-x-2">
                        <button className="text-gray-600 hover:text-gray-900 transition-colors">
                          View
                        </button>
                        <button className="text-gray-600 hover:text-gray-900 transition-colors">
                          Resend
                        </button>
                        {card.status === 'active' && (
                          <button className="text-red-600 hover:text-red-900 transition-colors">
                            Void
                          </button>
                        )}
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>

      {/* Summary Stats */}
      <div className="mt-8 grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Total Cards</h3>
          <p className="text-2xl font-bold text-gray-900">{filteredCards.length}</p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Total Value Sold</h3>
          <p className="text-2xl font-bold text-green-600">
            {formatCurrency(filteredCards.reduce((sum, c) => sum + c.amount, 0))}
          </p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Outstanding Balance</h3>
          <p className="text-2xl font-bold text-blue-600">
            {formatCurrency(filteredCards.reduce((sum, c) => sum + c.remainingBalance, 0))}
          </p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Redeemed Value</h3>
          <p className="text-2xl font-bold text-gray-900">
            {formatCurrency(
              filteredCards.reduce((sum, c) => sum + (c.amount - c.remainingBalance), 0)
            )}
          </p>
        </div>
      </div>

      {/* Recent Activity */}
      <div className="mt-8 bg-white rounded-lg shadow">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">Recent Gift Card Activity</h3>
        </div>
        <div className="p-6">
          {giftCards.length === 0 ? (
            <p className="text-gray-500 text-center py-4">No gift card activity yet.</p>
          ) : (
            <div className="space-y-4">
              {giftCards
                .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())
                .slice(0, 5)
                .map((card) => (
                  <div key={card.id} className="flex items-center justify-between py-2 border-b border-gray-100 last:border-b-0">
                    <div className="flex items-center space-x-4">
                      <div className="w-10 h-10 bg-green-100 rounded-full flex items-center justify-center">
                        <span className="text-green-600 font-medium text-sm">GC</span>
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">
                          {formatCurrency(card.amount)} gift card purchased
                        </p>
                        <p className="text-xs text-gray-500">
                          by {card.purchaserName} • {formatDate(card.createdAt)}
                        </p>
                      </div>
                    </div>
                    <div className="text-right">
                      <span className={getStatusBadge(card.status)}>
                        {card.status}
                      </span>
                    </div>
                  </div>
                ))}
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
